import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';

const INGREDIENT_PRICES = {
    salad: 0.5,
    cheese: .4,
    meat: 1.3,
    bacon: .7


}

class BurgerBuilder extends Component {
    // constructor(props) {
    //     super(props);
    //     this.state = {

    //     }
    // }
    state = {
        ingredients: {
            salad: 0,
            bacon: 0,
            cheese: 0,
            meat: 0
        },
        totalPrice: 4,
        purchasable: false,
        purchasing: false
    }

    addIngredientHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        const updatedCounted = oldCount + 1;
        const updatedIngredient = {
            ...this.state.ingredients
        }
        updatedIngredient[type] = updatedCounted;
        const priceAddition = INGREDIENT_PRICES[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice + priceAddition;

        this.setState({
            totalPrice: newPrice,
            ingredients: updatedIngredient
        })
        this.updatePurchaseState(updatedIngredient);

    }
    removeIngredientHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        var buffUpatedCounted = 0;
        var buffpriceAddition = 0;
        if (oldCount > 0) {
            buffUpatedCounted = oldCount - 1;
            buffpriceAddition = INGREDIENT_PRICES[type];
        }
        const updatedCounted = buffUpatedCounted;
        const updatedIngredient = {
            ...this.state.ingredients
        }
        updatedIngredient[type] = updatedCounted;
        const priceAddition = buffpriceAddition;
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice - priceAddition;

        this.setState({
            totalPrice: newPrice,
            ingredients: updatedIngredient
        })
        this.updatePurchaseState(updatedIngredient);

    }
    updatePurchaseState = (update) => {
        const ingredients = {
            ...update
        }
        // turns it into array of stringkeys
        const sum = Object.keys(ingredients)
            .map(igKey => {
                return ingredients[igKey]
            })
            .reduce((sum, el) => {
                return sum + el;
            }, 0);

        this.setState({
            purchasable: sum > 0
        })
    }
    purchaseHandler = () => {
        this.setState({
            purchasing: true
        });   
    }
    closeModal = () => {
        this.setState({
            purchasing: false
        });  
    }
    purchaseContinueHandler = () =>{
        alert('you continue!');
    }

    render() {
        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.closeModal}> 
                    <OrderSummary
                    ingredients={this.state.ingredients} 
                    purchaseCancelled={this.closeModal}
                    purchaseContinued={this.purchaseContinueHandler}
                    price={this.state.totalPrice}
                    
                    />
                </Modal>
                <Burger ingredients={this.state.ingredients} /> 
                <BuildControls 
                    purchasable={this.state.purchasable}
                    price={this.state.totalPrice} 
                    ingredientAdded={this.addIngredientHandler} 
                    ingredientRemoved={this.removeIngredientHandler} 
                    ordered={this.purchaseHandler}
                />
                
            </Aux>

        );
    }
}

export default BurgerBuilder;


