import React from 'react';

import Aux from '../../../hoc/Aux';
import Button from '../../UI/Button/Button';

const orderSummary = (props) => {
    // we want it in the format of <li>  salad: 1 </li>

    const ingredientSummary = Object.keys(props.ingredients).map(igKey => {
        return (
        <li><span style={{ textTransform: 'capitalize' }}>
            {igKey} : 
        </span> {props.ingredients[igKey]} </li>
        );
    });
    console.log(ingredientSummary);
    return (
        <Aux>

            <h3> Your Order </h3>
            <p> A delicious burger with the following ingredients: </p>
            <ul>
                {ingredientSummary}
            </ul>
            <p><strong>Total Price: {props.price}</strong></p>
            <p> Continue to Checkout? </p>
            <Button type='Danger' clicked={props.purchaseCancelled}> CANCEL </Button>
            <Button type='Success' clicked={props.purchaseContinued}> CONTINUE </Button>
        </Aux>
    )
}

export default orderSummary;


