import React from 'react';  
// make web pack aware of fact that we are using this iamge - and webpack will handle it with special modeule
// copy it over the destinatoin directory it creates and optimize the image

import burgerLogo from '../../assets/images/burger-logo.png';
import classes from './Logo.css';

const logo = (props) => {
    return (
        <div className={classes.Logo}>
            <img src={burgerLogo}/>
        </div>
    );
}
export default logo;


